.App{
  text-align: center;
  width: 100%;
  /* height: 100vh; */
}

.pokemonCardContainer {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  place-items: center;
  gap: 20px;
  margin-top: 20px;

}

.btn {
  padding: 30px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;

}

.btn button{
  padding: 13px 32px;
  background-color: aqua;
  border: none;
  box-shadow: 6px 4px 20px 6px rgba(17, 17, 26, 0.18);
  border-radius: 5px;
  color: white;
  cursor: pointer;
  transition: all 0.3s;
}

.btn button:hover{
  transform: translateY(5px);
  box-shadow: none;
}
